@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Cedarville+Cursive&family=Dancing+Script:wght@600&family=Fraunces:ital,opsz,wght@1,9..144,100&family=Libre+Baskerville&family=Open+Sans:wght@300&family=Oswald:wght@200&family=Poppins:wght@100;300;500&family=Roboto+Condensed:wght@300&family=Rubik&family=Rubik+Dirt&display=swap');
.headerr{
    background-color: #ffc107;
    padding: 1rem;
    font-family: 'Libre Baskerville', serif;
    font-size: 18px;
    box-shadow: 0px 4px 3px 0px rgba(30, 30, 30, 0.453);
    position:fixed;
    width: 100%;
    z-index: 10;
    top: 0;
}
a{
    text-decoration: none;
    color: black;
}
a:hover{
    color: rgba(0, 0, 0, 0.93);
}
.icon {
    text-align: center;
}
.icon img{
    max-width: 4.55rem;
    width: 50%;
}
.headcont{
    display: flex;
    align-items: center;
    justify-items: center;
}
